import React, { useState, useCallback } from 'react';
import {
  View,
  Text,
  TouchableOpacity,
  StyleSheet,
  Alert,
  Image,
  ActivityIndicator,
} from 'react-native';
import ImagePicker from 'react-native-image-crop-picker';
import { ButtonBase } from '../ButtonBase';

export interface ImagePickerProps {
  /**
   * Callback function called when image is selected
   */
  onImageSelected?: (image: ImageData) => void;
  /**
   * Callback function called when there's an error
   */
  onError?: (error: string) => void;
  /**
   * Whether to show crop interface
   */
  enableCrop?: boolean;
  /**
   * Crop width
   */
  cropWidth?: number;
  /**
   * Crop height
   */
  cropHeight?: number;
  /**
   * Maximum file size in bytes
   */
  maxFileSize?: number;
  /**
   * Image quality (0-1)
   */
  quality?: number;
  /**
   * Whether to include base64 data
   */
  includeBase64?: boolean;
  /**
   * Custom button text
   */
  buttonText?: string;
  /**
   * Custom button style
   */
  buttonStyle?: any;
  /**
   * Custom text style
   */
  textStyle?: any;
  /**
   * Whether to show preview
   */
  showPreview?: boolean;
  /**
   * Preview image style
   */
  previewStyle?: any;
  /**
   * Whether to allow multiple selection
   */
  multiple?: boolean;
  /**
   * Maximum number of images when multiple is true
   */
  maxFiles?: number;
  /**
   * Whether to compress image
   */
  compress?: boolean;
  /**
   * Compression quality (0-1)
   */
  compressQuality?: number;
  /**
   * Whether to show loading indicator
   */
  showLoading?: boolean;
  /**
   * Custom loading component
   */
  loadingComponent?: React.ReactNode;
  /**
   * Whether to show action sheet
   */
  showActionSheet?: boolean;
  /**
   * Custom action sheet options
   */
  actionSheetOptions?: {
    title?: string;
    options?: string[];
    cancelButtonIndex?: number;
    destructiveButtonIndex?: number;
  };
}

export interface ImageData {
  uri: string;
  width: number;
  height: number;
  mime: string;
  size: number;
  filename?: string;
  path: string;
  data?: string; // base64 data if includeBase64 is true
}

export const ImagePickerComponent: React.FC<ImagePickerProps> = ({
  onImageSelected,
  onError,
  enableCrop = false,
  cropWidth = 300,
  cropHeight = 300,
  maxFileSize = 10 * 1024 * 1024, // 10MB
  quality = 0.8,
  includeBase64 = false,
  buttonText = 'Chọn ảnh',
  buttonStyle,
  textStyle,
  showPreview = true,
  previewStyle,
  multiple = false,
  maxFiles = 5,
  compress = true,
  compressQuality = 0.8,
  showLoading = true,
  loadingComponent,
  showActionSheet = true,
  actionSheetOptions = {
    title: 'Chọn ảnh từ',
    options: ['Camera', 'Thư viện ảnh', 'Hủy'],
    cancelButtonIndex: 2,
  },
}) => {
  const [selectedImages, setSelectedImages] = useState<ImageData[]>([]);
  const [isLoading, setIsLoading] = useState(false);

  const handleError = useCallback(
    (error: any) => {
      console.error('ImagePicker Error:', error);
      const errorMessage = error.message || 'Có lỗi xảy ra khi chọn ảnh';
      onError?.(errorMessage);
      Alert.alert('Lỗi', errorMessage);
    },
    [onError]
  );

  const validateImage = useCallback(
    (image: any): boolean => {
      if (image.size > maxFileSize) {
        const errorMessage = `Kích thước ảnh vượt quá giới hạn ${Math.round(
          maxFileSize / (1024 * 1024)
        )}MB`;
        handleError({ message: errorMessage });
        return false;
      }
      return true;
    },
    [maxFileSize, handleError]
  );

  const processImage = useCallback(
    (image: any): ImageData => {
      const processedImage: ImageData = {
        uri: image.path,
        width: image.width,
        height: image.height,
        mime: image.mime,
        size: image.size,
        filename: image.filename,
        path: image.path,
      };

      if (includeBase64 && image.data) {
        processedImage.data = image.data;
      }

      return processedImage;
    },
    [includeBase64]
  );

  const openCamera = useCallback(async () => {
    try {
      setIsLoading(true);
      const options: any = {
        mediaType: 'photo',
        quality: quality,
        includeBase64: includeBase64,
        compressImageQuality: compress ? compressQuality : 1,
      };

      if (enableCrop) {
        options.cropping = true;
        options.width = cropWidth;
        options.height = cropHeight;
      }

      const image = await ImagePicker.openCamera(options);

      if (validateImage(image)) {
        const processedImage = processImage(image);
        setSelectedImages([processedImage]);
        onImageSelected?.(processedImage);
      }
    } catch (error: any) {
      if (error.code !== 'E_PICKER_CANCELLED') {
        handleError(error);
      }
    } finally {
      setIsLoading(false);
    }
  }, [
    quality,
    includeBase64,
    compress,
    compressQuality,
    enableCrop,
    cropWidth,
    cropHeight,
    validateImage,
    processImage,
    onImageSelected,
    handleError,
  ]);

  const openGallery = useCallback(async () => {
    try {
      setIsLoading(true);
      const options: any = {
        mediaType: 'photo',
        quality: quality,
        includeBase64: includeBase64,
        compressImageQuality: compress ? compressQuality : 1,
        multiple: multiple,
      };

      if (multiple && maxFiles > 1) {
        options.maxFiles = maxFiles;
      }

      if (enableCrop && !multiple) {
        options.cropping = true;
        options.width = cropWidth;
        options.height = cropHeight;
      }

      const images = await ImagePicker.openPicker(options);
      const imageArray = Array.isArray(images) ? images : [images];

      const validImages = imageArray.filter(validateImage);
      const processedImages = validImages.map(processImage);

      if (processedImages.length > 0) {
        setSelectedImages(processedImages);
        if (multiple) {
          onImageSelected?.(processedImages as any);
        } else {
          onImageSelected?.(processedImages[0]!);
        }
      }
    } catch (error: any) {
      if (error.code !== 'E_PICKER_CANCELLED') {
        handleError(error);
      }
    } finally {
      setIsLoading(false);
    }
  }, [
    quality,
    includeBase64,
    compress,
    compressQuality,
    multiple,
    maxFiles,
    enableCrop,
    cropWidth,
    cropHeight,
    validateImage,
    processImage,
    onImageSelected,
    handleError,
  ]);

  const showImagePicker = useCallback(() => {
    if (showActionSheet) {
      Alert.alert(
        actionSheetOptions.title || 'Chọn ảnh từ',
        '',
        [
          {
            text: actionSheetOptions.options?.[0] || 'Camera',
            onPress: openCamera,
          },
          {
            text: actionSheetOptions.options?.[1] || 'Thư viện ảnh',
            onPress: openGallery,
          },
          {
            text: actionSheetOptions.options?.[2] || 'Hủy',
            style: 'cancel',
          },
        ],
        { cancelable: true }
      );
    } else {
      openGallery();
    }
  }, [showActionSheet, actionSheetOptions, openCamera, openGallery]);

  const removeImage = useCallback(
    (index: number) => {
      const newImages = selectedImages.filter((_, i) => i !== index);
      setSelectedImages(newImages);
      if (multiple) {
        onImageSelected?.(newImages as any);
      } else if (newImages.length > 0) {
        onImageSelected?.(newImages[0]!);
      }
    },
    [selectedImages, multiple, onImageSelected]
  );

  const renderButtonContent = () => {
    if (isLoading && showLoading) {
      if (loadingComponent) {
        return loadingComponent;
      }

      return (
        <View style={styles.buttonLoadingContainer}>
          <ActivityIndicator size="small" color="#FFFFFF" />
          <Text style={[styles.buttonText, textStyle, styles.loadingText]}>
            Đang xử lý...
          </Text>
        </View>
      );
    }

    return <Text style={[styles.buttonText, textStyle]}>{buttonText}</Text>;
  };

  const renderPreview = () => {
    if (!showPreview || selectedImages.length === 0) return null;

    return (
      <View style={styles.previewContainer}>
        {selectedImages.map((image, index) => (
          <View key={index} style={styles.imageContainer}>
            <Image
              source={{ uri: image.uri }}
              style={[styles.previewImage, previewStyle]}
            />
            <TouchableOpacity
              style={styles.removeButton}
              onPress={() => removeImage(index)}
            >
              <Text style={styles.removeButtonText}>×</Text>
            </TouchableOpacity>
          </View>
        ))}
      </View>
    );
  };

  return (
    <View style={styles.container}>
      <ButtonBase
        onPress={showImagePicker}
        style={[styles.button, buttonStyle]}
        disabled={isLoading}
      >
        {renderButtonContent()}
      </ButtonBase>

      {renderPreview()}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    alignItems: 'center',
  },
  button: {
    backgroundColor: '#007AFF',
    paddingHorizontal: 20,
    paddingVertical: 12,
    borderRadius: 8,
    minWidth: 120,
  },
  buttonText: {
    color: '#FFFFFF',
    fontSize: 16,
    fontWeight: '600',
    textAlign: 'center',
  },
  buttonLoadingContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  loadingText: {
    marginLeft: 8,
    opacity: 0.8,
  },
  previewContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    marginTop: 15,
    justifyContent: 'center',
  },
  imageContainer: {
    position: 'relative',
    margin: 5,
  },
  previewImage: {
    width: 80,
    height: 80,
    borderRadius: 8,
    backgroundColor: '#f0f0f0',
  },
  removeButton: {
    position: 'absolute',
    top: -5,
    right: -5,
    backgroundColor: '#FF3B30',
    borderRadius: 12,
    width: 24,
    height: 24,
    justifyContent: 'center',
    alignItems: 'center',
  },
  removeButtonText: {
    color: '#FFFFFF',
    fontSize: 16,
    fontWeight: 'bold',
  },
});

export default ImagePickerComponent;
